import React, { useState,useCallback } from "react";
import Cropper from "react-easy-crop";
import { DialogContent, DialogActions, Box,Button,Typography,Slider } from "@mui/material";
import {getCroppedImg} from "./utils/cropImage"
import Modal from '@mui/material/Modal';
import { UserinfoAction } from "../../redux/slices/Userinfo"
import { useDispatch } from "react-redux";

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: "auto",
  bgcolor: 'var(--color-light)',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};


const CropEasy = ({ photoURL, setOpenCrop }) => {
	const [crop, setCrop] = useState({ x: 0, y: 0 })
	const [zoom, setZoom] = useState(1)
	const [rotation, setRotation] = useState(0)
	const [croppedAreaPixels, setCroppedAreaPixels] = useState(null)
	const [croppedImage, setCroppedImage] = useState();
	const profile = true
	const dispatch = useDispatch();

	const cropComplete = (croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels);
    console.log(croppedArea, croppedAreaPixels)
  };

 const cropImage = useCallback(async () => {
  	 try {
      const croppedImag = await getCroppedImg(
        photoURL,
        croppedAreaPixels,
        rotation
      );
      setCroppedImage(croppedImag);
     console.log("donee", { croppedImage });
     // dispatch(UserinfoAction.ChangeProfile(croppedImage))
    } catch (e) {
      console.error(e);
    }
  	},
  	[croppedAreaPixels,croppedImage,rotation,photoURL],
  )
	return <>
			<Modal
			open={true}
			aria-labelledby="modal-modal-title"
			aria-describedby="modal-modal-description"
			>
	   <Box sx={style}>
		<Box sx={{ background: "white", position: "relative", height: 300, width:"100%", minWidth: { sm: 400 } }}>
			<Cropper
				image={photoURL}
				crop={crop}
				rotation={rotation}
				zoom={zoom}
				aspect={profile ? `${3/3}`:`${3/5}`}
				zoomWithScroll={true}
				onZoomchange={setZoom}
				onRotationChange={setRotation}
				onCropChange={setCrop}
				onCropComplete={cropComplete}
				objectFit={"contain"}
			/>
		</Box>
		<DialogActions sx={{ flexDirection: "column", nx: 3, my: 2 }}>
			<Box sx={{ width: "100%", nb: 1 }}>
				<Box>
					<Typography>zoom : {zoomParcent(zoom)}</Typography>
					<Slider
						valuelabledisplay="auto"
						valuelablelformat={zoomParcent}
						min={1}
						max={3}
						step={0.01}
						value={zoom}
						onChange={(e, zoom) => setZoom(zoom)}
					/>
				</Box>
				<Box>
					<Typography>rotation : {rotation}</Typography>
					<Slider
						valuelableldisplay="auto"
						min={0}
						max={360}
						value={rotation}
						onChange={(e, rotation) => setRotation(rotation)}
					/>
				</Box>
				<Box>
						<Typography>rotation : {rotation}</Typography>
						<Slider
						valuelableldisplay="auto"
						min={0}
						max={360}
						value={rotation}
						onChange={(e, rotation) => setRotation(rotation)}
						/>
				</Box>
			</Box>
			<Box sx={{
				display: "flex",
				gap: 10,
				flexWrap: "wrap",
				justifyContent:"space-between"
			}}>
				<Button
					variant="outlined"
					onClick={() => setOpenCrop(false)}
				>Cancel
				</Button>
				<Button
					variant="contained"
					onClick={cropImage}
				>
					Crop
				</Button>

			</Box>
		</DialogActions>
		</Box>
		</Modal>
	</>
}


export default CropEasy;

const zoomParcent = (value) => {
	return `${Math.round(value * 100)}%`
}
