import React,{useEffect,useState,useContext} from "react"
import Helmet from "../commponents/Helmet/Helmet";
import {Upload,Button,message,Progress,Space,Typography} from "antd"
import {initializeApp} from "firebase/app";
import {getStorage} from "firebase/storage";
import RightBar from "../commponents/Right/RightBar";
import { uidContext } from "../commponents/AppContext"
import FollowHandle from "../commponents/profile/FollowHandle";
import Posts from "../commponents/Posts/Posts";
import {isEmpty} from "../commponents/Utils"
import { Avatar } from "@mui/material";
import { GetAllposts } from '../redux/slices/Allposts';
import { getusersAction } from '../redux/slices/Getusers';
import {UserinfoAction} from "../redux/slices/Userinfo"
import {Link} from "react-router-dom";
import {SERVER_URL} from "../Domain"
import { getOrientation } from 'get-orientation/browser'
import CropEasy from "../commponents/crop/CropEasy"
import axios from "axios"
import { useDispatch } from "react-redux";
import {getRotatedImage} from "../commponents/crop/utils/cropImage"
import {useSelector} from "react-redux"
import {ref,uploadBytesResumable,getDownloadURL} from "@firebase/storage";
const ProfileOther = ({socket})=>{
        const user = useSelector((state) => state.userInfo)
        const userinfo = useSelector((state) => state.userInfo)
        const usersData = useSelector((state) => state.getusers)
        const Allpost = useSelector((state) => state.Allpost)
        const [hisFollowers ,setHisFollowers] =useState()
        const [hisLikes ,setHisLikes] =useState()
        const [hisPosts ,sethisPosts] =useState()
        const [editprofile,seteditprofile] = useState(false)
        const [photoURL,setPhotoURL]=useState(user?.picture)
        const [openCrop,setOpenCrop]=useState(false)
        const [fullName,setfullName]=useState("")
        const [phone,setPhone]=useState("")
        const [bio,setBio]=useState("")
        const [professionnal,setProfessionnal]=useState("")
        const [suivante,setsuivante] = useState(false)
        const dispatch = useDispatch()
        const [progress,setProgress] = useState(0);
        const [estimated,setEstimated] = useState(0);
        const uid = useContext(uidContext)
        const [state,setState]= useState({
          fileList:[],
          uploading:false,
        })
        const [isThumber,setThumber] = useState(false)
        const ORIENTATION_TO_ANGLE = {
        '3': 180,
        '6': 90,
        '8': -90,
        }
          function readFile(file) {
          return new Promise((resolve) => {
          const reader = new FileReader()
          reader.addEventListener('load', () => resolve(reader.result), false)
          reader.readAsDataURL(file)
          })
          }
      useEffect(()=>{
              if(hisFollowers?.length > 0){
              const carousel = document.querySelector(".carousel");
              const arrowsBtns = document.querySelectorAll(".himfollowers i");
              arrowsBtns.forEach((btn) => {
              btn.addEventListener("click", (e) => {
              if (e.target.id === "left") {
              carousel.scrollLeft -= 10;
              } else {
              carousel.scrollLeft += 10;
              }
              });
              });
              let isDragging = false,
              startX,
              startScrollLeft;
              const dragstart = (e) => {
              isDragging = true;
              carousel.classList.add("dragging");
              startX = e.pageX;
              startScrollLeft = carousel.scrollLeft;
              };
              const dragstop = () => {

              isDragging = false;
              carousel.classList.remove("dragging");
              };
              const dragging = (e) => {
              if (!isDragging) return;
              carousel.scrollLeft = startScrollLeft - (e.pageX - startX);
              };
              carousel.addEventListener("mousedown", dragstart);
              carousel.addEventListener("mousemove", dragging);
              document.addEventListener("mouseup", dragstop);
              }
  },[hisFollowers?.length])
      // ================================= essaie filebase ==============================================
       const firebaseConfig = {
        apiKey: "AIzaSyBTPpOH0XcHRsTIDe_gfaZzXLXM5ubYhAg",
        authDomain: "chat-burundi.firebaseapp.com",
        projectId: "chat-burundi",
        storageBucket: "chat-burundi.appspot.com",
        messagingSenderId: "735028236904",
        appId: "1:735028236904:web:d54558f034e3a639e361dd",
        measurementId: "G-DH7T4PP3N5"
    };
   const app = initializeApp(firebaseConfig);
   const storage = getStorage(app);
      // ================================= essaie filebase ==============================================

  //fetch all userdata from data base
  useEffect(()=>{
    if(uid){
    const fetchUserData = async () => {
      if (uid !== "no token") {
        await axios({
          method: "get",
          url: `${SERVER_URL}/api/user/${uid}`,
          withCredentials: true,
        })
          .then(({data}) => {
           dispatch(UserinfoAction.fetchUserInfo(data))
          })
          .catch(() => console.log("no data found"))
      }
    }
    fetchUserData()
    }
    //fetch all data's user
        const fetchUserAllData = async () => {
      if (uid !== "no token") {
        await axios({
          method: "get",
          url: `${SERVER_URL}/api/user`,
          withCredentials: true,
        })
          .then((res) => {
            dispatch(getusersAction.GetuAllsers(res.data))
          })
          .catch(() => dispatch(getusersAction.GetuAllsers({ message: "no user exist now" })))
      }
    }
    fetchUserAllData()
    //fetch all post data
        const fetchUserAllDataPost = async () => {
      if (uid !== "no token") {
        await axios({
          method: "get",
          url: `${SERVER_URL}/api/post`,
          withCredentials: true,
        })
          .then(({data}) => {
            dispatch(GetAllposts.GetAll(data))
          })
          .catch(() => dispatch(GetAllposts.GetAll({ message: "no post exist now" })))
      }
    }
    fetchUserAllDataPost() 
  },[uid])
         useEffect(()=>{
          if((uid !== "no token" || Allpost) && !isEmpty(usersData) ){
            
            var myFollowers = usersData?.filter((user)=> user.following.includes(uid))
            setHisFollowers(myFollowers)
            var myPosts  = Allpost.filter((post)=>post.posterId === uid)
            sethisPosts(myPosts)
          
          var count = 0;
          for (let index = 0; index < hisPosts?.length; index++) {
          count = count + hisPosts[index].likers.length
           }
           setHisLikes(count)
        }
       },[uid,Allpost])
          const handlePicture = async(e)=>{
            const file = e.target.files[0];
             setThumber(false);
          if(file){
              setState({fileList : [file]})
            let imageDataUrl = await readFile(file)
                try {
                // apply rotation if needed
                const orientation = await getOrientation(file)
                const rotation = ORIENTATION_TO_ANGLE[orientation]
                if (rotation) {
                imageDataUrl = await getRotatedImage(imageDataUrl, rotation)
                }
                } catch (e) {
                console.log('failed to detect the orientation')
                }
            setOpenCrop(false)//to open clop model 
            setPhotoURL(imageDataUrl)
            dispatch(UserinfoAction.ChangeProfile(imageDataUrl));
          }
        }
      const handlePictureThumber = async(e)=>{
            const file = e.target.files[0];
            setThumber(true);
          if(file){
          setState({fileList : [file]})
            let imageDataUrl = await readFile(file)
                try {
                // apply rotation if needed
                const orientation = await getOrientation(file)
                const rotation = ORIENTATION_TO_ANGLE[orientation]
                if (rotation) {
                imageDataUrl = await getRotatedImage(imageDataUrl, rotation)
                }
                } catch (e) {
                console.log('failed to detect the orientation')
                }
            setOpenCrop(false)//to open clop model 
            setPhotoURL(imageDataUrl)
            dispatch(UserinfoAction.ChangeProfileThumber(imageDataUrl));
          }
        }
        const updateFullname = async()=>{
         if(isEmpty(fullName)){
          message.error("empty full name");
         }else{
          await axios({
          method: "put",
          url: `${SERVER_URL}/api/user/${uid}`,
          withCredentials: true,
          data : {fullName}
        })
          .then(({data}) => {
           console.log(data)
           message.success("fullName has been update successifull")
          })
          .catch(() => console.log("no data found"))
      }
         }
    const updatePhone = async()=>{
         if(isEmpty(phone)){
          message.error("empty phone");
         }else{
          await axios({
          method: "put",
          url: `${SERVER_URL}/api/user/${uid}`,
          withCredentials: true,
          data : {phone}
        })
          .then(({data}) => {
           console.log(data)
            message.success("phone has been update successifull")
          })
          .catch(() => console.log("no data found"))
      }
         }
    const updateProfessionnal = async()=>{
         if(isEmpty(professionnal)){
          message.error("empty professionnal");
         }else{
          await axios({
          method: "put",
          url: `${SERVER_URL}/api/user/${uid}`,
          withCredentials: true,
          data : {professionnal}
        })
          .then(({data}) => {
            message.success("professionnal has been update successifull")
          })
          .catch(() => console.log("no data found"))
      }
         }
    const updateBio = async()=>{
         if(isEmpty(bio)){
          message.error("empty biographie");
         }else{
          await axios({
          method: "put",
          url: `${SERVER_URL}/api/user/${uid}`,
          withCredentials: true,
          data : {bio}
        })
          .then(({data}) => {
            message.success("biographie has been update successifull")
          })
          .catch(() => console.log("no data found"))
      }
         }

      const handleUpload = async()=>{
        const {fileList} = state;
        const formData = new FormData();
        formData.append("userId",uid);
        !isEmpty(fileList) && formData.append("file",fileList[0])
        setState({uploading : true});
          await axios({
          method: "post",
          url:`${SERVER_URL}/api/user/upload`,
          withCredentials: true,
          data : formData,
          onUploadProgress:(event)=>{
          console.log(event)
          setProgress(event.progress);
          setEstimated(event.estimated);
          },
        })
          .then(({data}) => {
           console.log(data)
           dispatch(UserinfoAction.ChangeProfile(data.picture));
           setState({fileList:[]});
           message.success('upload successifull');
           setState({uploading : false});
          })
          .catch((res) => {
            setState({uploading : false});
            message.error(res.response.data.message);
          })
        };
//fire base
      const handleUploadThumber = async()=>{
        const {fileList} = state;
        const timeStarted = new Date();
        setState({uploading : true});
          console.log(fileList[0])
          if(!fileList[0]) return
            const storageRef = ref(storage,`profil/${fileList[0].name}`)
          const uploadTask = uploadBytesResumable(storageRef,fileList[0]);

          uploadTask.on(
           "state_changed",
           (snapshot)=>{
            if(snapshot.bytesTransferred){
            var progress = snapshot.bytesTransferred / snapshot.totalBytes;
             setProgress(progress)
                var timeElapsed = (new Date()) - timeStarted;
                var uploadSpeed = snapshot.bytesTransferred / (timeElapsed/1000); 
                var estimate = (snapshot.totalBytes - snapshot.bytesTransferred) / uploadSpeed; 
               setEstimated(estimate)
             }
           },
           (err)=>{
            console.log(err);
            setState({uploading : false});
           },
           ()=>{
            getDownloadURL(uploadTask.snapshot.ref).then(async(url)=>{
              const newData = {
                "userId":uid,
                "file":url
              }
                  await axios({
                  method: "post",
                  url:`${SERVER_URL}/api/user/upload/thumber`,
                  withCredentials: true,
                  data : newData,
                })
                  .then(({data}) => {
                   dispatch(UserinfoAction.ChangeProfileThumber(data.thumber));
                   setState({fileList:[]});
                   message.success('upload successifull');
                   setState({uploading : false});
                   setEstimated(0);
                  })
                  .catch((res) => {
                    setState({uploading : false});
                    message.error(res.response.data.message);
                  })
});
})}


      const {fileList} = state;
      const props = {
        multiple:false,
        showUploadList:true,
        onRemove : (file)=>{
          setState((state)=>{
            return{
              fileList:[]
            };
          });
        },
        beforeUpload: (file)=>{
          setState((state)=>({
            fileList : [file],
          }))
          return false;
        },
        fileList,
      };
      const getTimeToString = (timeInSecondes)=>{
        const hours = Math.floor(timeInSecondes /3600);
        const minutes = Math.floor(timeInSecondes / 60 - hours * 60);
        const seconds = Math.floor(timeInSecondes - minutes * 60 -hours * 3600);
       let timeString = `${seconds} sec`;
       if(minutes){
        timeString = `${minutes} min ${timeString}`;
       }
        if(minutes){
        timeString = `${hours} hrs ${timeString}`;
       }
       return timeString;
      }
  useEffect(()=>{
  if(socket && user && user.pseudo){
  socket.emit("newuser",user._id)
            var root = document.querySelector(":root")
            var colorModel = JSON.parse(localStorage.getItem(`${user._id}`))
            if(colorModel){
            if(colorModel.primaryHue){
            root.style.setProperty("--color-primary-hue",colorModel.primaryHue)
            } 
            if(colorModel.font_size){document.querySelector("html").style.fontSize=colorModel.font_size};
            if(colorModel.lightcolorlightness){root.style.setProperty("--light-color-lightness",colorModel.lightcolorlightness)}
            if(colorModel.whitecolorlightness){root.style.setProperty("--white-color-lightness",colorModel.whitecolorlightness)}
            if(colorModel.darkcolorlightness){root.style.setProperty("--dark-color-lightness",colorModel.darkcolorlightness) }
            if(colorModel.colornamelightness){root.style.setProperty("--color-name-lightness",colorModel.colornamelightness) }

            }
}
if(uid ==="no token"){
  window.location = "/login"
}
},[socket,user.pseudo,user,uid])

   return(
    <Helmet title={user?.pseudo}>
    <div className="container">
      <div className="wrapper">
        <div style={{display:"flex",flexDirection:"column",gap:"1rem"}}>
        <div className="profile">
          <div className="image">
            <img src={userinfo?.thumber} alt={userinfo && userinfo?._id}/>
          </div>
          <div className="wrapper-2">
          <div style={
          {
          width:"9rem",
          height:"9rem",
          overflow:"hidden",
          borderRadius:"50%",
          position:"relative",
          top:"-70px",
          left:"20px",
          border:"6px solid var(--color-white)"

          }
        }>
           <Avatar 
           style={{
            display:"absolute",
            top:"0",
            left:"0",
            width:"100%",
            height:"100%"
           }}
           src={userinfo?.picture} alt={userinfo?.pseudo}/>
         </div> 
         <div className="infos">
          <h4>{userinfo?.pseudo}</h4>
          <span>{"Étudiant(e) à univeriste polytechinique de gitega(UPG)"}</span>
          <span>{userinfo?.professionnal}</span>
          <small>kinyinya,ruyigi,burundi</small>
           <span>bio : {userinfo?.bio}</span>
          <div className="followFollowing">
          <span>{isEmpty(userinfo?.followers?.length) ? "0":userinfo?.followers?.length} followers</span> 
          <span>{isEmpty(userinfo?.following?.length) ? "0" : userinfo?.following?.length } following</span> 
          <span>{isEmpty(hisPosts?.length) ? "0": hisPosts?.length} Posts</span> 
          <span>{hisLikes} Likes</span>  
          </div>
            <div className="action">
            <FollowHandle
               idTofollow={uid}
               type={"suggestion"}
                                />
                                {" "}
           <button className=" btn-new" onClick={()=>seteditprofile(true)}>Edit Profile</button>
        </div>
         </div>
          </div>
        </div>
         {hisFollowers?.length > 0 && <div className="himfollowers">
         {hisFollowers?.length > 3 && <i className="ri-arrow-drop-left-line" id="left"></i>}
         <div className="carousel">
         {
            hisFollowers?.map((user,index)=>(
         <div className="request" key={index}>
            <div className="info">
               <div className="profile-picture">
               <Avatar src={user?.picture} alt="aa" />
               </div>
               <div>
              <Link className="linkProfile" to={`/profile/${user?._id}`}><h5 className="noum">{user?.pseudo}</h5></Link>
            <p className="text-muted">
               {user?.following.length} following
            </p>
            </div>
            </div>
            <div className="action">
            <FollowHandle
            idTofollow={user._id}
            type={"suggestion"}
                           />
            <button className=" btn-new">gretting</button>
           </div>
            </div>
         ))}
         </div>
         {hisFollowers?.length > 3 && <i className="ri-arrow-drop-right-line" id="right"></i>}
         </div>}
        <div>
        <Posts socket={socket} type={"hisprofile"} paramsId={uid}/>
        </div>
        </div>
        <div className="leftProfile">{user && <RightBar user={user}/>}</div>
      </div>
    </div>
    {
     openCrop &&
     <div className="popupUpdateProfileCrop">
      <CropEasy {...{photoURL,setOpenCrop}}/>
    </div>
      
    }

    {
      (editprofile) &&
          <div className="popupUpdateProfile">
      <div className="un">
        <div className="un-un"><h3>User infos</h3></div>
        <div className="un-deux">
          <input type="text" placeholder="full name" value={fullName} onChange={(e)=>setfullName(e.target.value)} />
          <button className="btnprofile" onClick={updateFullname}>update</button>
        </div>
        <div className="un-trois">
          <input type="text" placeholder="number phone ie: +257 11449900" value={phone} onChange={(e)=>setPhone(e.target.value)} />
          <button className="btnprofile" onClick={updatePhone}>update</button>
        </div>
        <div className="un-quatre">
          <input type="text" placeholder="biographique" value={bio} onChange={(e)=>setBio(e.target.value)} />
          <button className="btnprofile" onClick={updateBio}>update</button>
        </div>
        <div className="un-cinq">
          <input type="text" placeholder="Your proffessional" value={professionnal} onChange={(e)=>setProfessionnal(e.target.value)}/>
          <button className="btnprofile" onClick={updateProfessionnal}>update</button>
        </div>
        <div className="un-six">
          <input type="text" placeholder="Your country" />
          <button className="btnprofile">update</button>
        </div>
        <div className="un-six">
          <input type="text" placeholder="where didu study???" />
          <button className="btnprofile">update</button>
        </div>
        <div className="un-six">
          <select>
            <option>Masculin</option>
            <option>feminin</option>
          </select>
          <button className="btnprofile">update</button>
        </div>
        <div className="un-un"><h3>Your social media</h3></div>
          <div className="un-six">
          <input type="text" placeholder="Add link of your social media like ie: https://www.chatburundi.com/janeirohurley" />
          <button className="btnprofile">Add</button>
          <div className="social-media">
            <div className="media-1"><i className="ri-facebook-circle-fill"></i><Link to="profile">facebook</Link><span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-twitter-fill"></i>twitter <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-instagram-fill"></i>instagram <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-youtube-fill"></i>youtube<span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-whatsapp-fill"></i>whatsapp <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-whatsapp-fill"></i>whatsapp <span><i className="ri-close-circle-fill"></i></span></div> 
          </div>
        </div>
        {
          suivante &&
        <div className="suivante">
        <button className="btnprofile precedente" onClick={()=>setsuivante(false)}><i className="ri-arrow-left-line"></i> precedente</button>
        </div>
        }

        <button className="btnprofile btnsuivante" onClick={()=>setsuivante(true)}> suivante <i className="ri-arrow-right-line"></i></button>
      </div>
      <div className="deux">
        <div className="deux-un"><h3>Current profile</h3></div>
            <div className="profile">
            <div className="image">
            <div className="popounderphoto">
              <div className="icon-input">
                <input type="file" title="update thumber" onChange={(e)=>handlePictureThumber(e)} />
                <i className="ri-pencil-line"></i>
              </div>
            </div>
            <img src={userinfo?.thumber} alt={userinfo && userinfo?._id}/>
            </div>
            <div className="wrapper-2">
            <div style={
            {
            width:"9rem",
            height:"9rem",
            overflow:"hidden",
            borderRadius:"50%",
            position:"relative",
            top:"-70px",
            left:"20px",
            border:"6px solid var(--color-white)"

            }
            }>
            <div className="popounderphoto roundprofile">
            <div className="icon-input2">
            <input type="file" title="update picture" onChange={(e)=>handlePicture(e)} />
            <i className="ri-pencil-line"></i>
            </div>
            </div>
            <Avatar 
            style={{
            display:"absolute",
            top:"0",
            left:"0",
            width:"100%",
            height:"100%"
            }}
            src={userinfo?.picture} alt={userinfo?.pseudo}/>
            </div> 
            <div className="infos">
            <Upload {...props}>
            </Upload>
            {estimated !== 0 &&
            <>
            <Space direction="column"
            style={{
            backgroundColor:"rgba(0,0,0,0.5)",
            padding:8,
            width:"100%",
            display:"flex",
            flexDirection:"column"
          }}>
          <Typography variant="span" type="secondary">is being Uploaded in {!estimated ? "calcurating.." : getTimeToString(estimated)}</Typography>
          <Progress percent={Math.ceil(progress * 100)} style={{display:"flex",flexDirection:"row"}}/>
          </Space>
           </>
           }
           {
            isThumber === false && <Button
            type="primary"
            onClick={handleUpload}
            disabled = {isEmpty(state.fileList)}
            loading = {state.uploading}
            style={{marginTop:16}}
            >
            {state.uploading ? "Uploading....":"start Upload profil"}
            </Button>
           }
           {
            isThumber === true && <Button
            type="primary"
            onClick={handleUploadThumber}
            disabled = {isEmpty(state.fileList)}
            loading = {state.uploading}
            style={{marginTop:16}}
            >
            {state.uploading ? "Uploading....":"start Upload thumber"}
            </Button>
           }

            <h4>{userinfo?.pseudo}</h4>
            <span><i className="ri-building-4-line"></i>{" Étudiant(e) à univeriste polytechinique de gitega(UPG)"}</span>
            <span><i className="ri-building-4-line"></i>{userinfo?.professionnal}</span>
            <small><i className="ri-map-pin-2-line"></i> kinyinya,ruyigi,burundi</small>
            <span> bio : {userinfo?.bio}</span>
            <div className="followFollowing">
          <span>{isEmpty(userinfo?.followers?.length) ? "0":userinfo?.followers?.length} followers</span> 
          <span>{isEmpty(userinfo?.following?.length) ? "0" : userinfo?.following?.length } following</span> 
          <span>{isEmpty(hisPosts?.length) ? "0": hisPosts?.length} Posts</span> 
          <span>{hisLikes} Likes</span> 
            </div>
                      <div className="social-media">
            <div className="media-1"><i className="ri-facebook-circle-fill"></i><Link to="profile">facebook</Link><span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-twitter-fill"></i>twitter <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-instagram-fill"></i>instagram <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-youtube-fill"></i>youtube<span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-whatsapp-fill"></i>whatsapp <span><i className="ri-close-circle-fill"></i></span></div>
            <div className="media-1"><i className="ri-whatsapp-fill"></i>whatsapp <span><i className="ri-close-circle-fill"></i></span></div> 
          </div>
            </div>
            </div>
            </div>

      </div>
    </div>
    }
     </Helmet>
    )
}

